<template>
    <v-card width="97%" class="pb-2"
        style="border-radius: 10px;"
    >
        <v-toolbar color="secondary">
            <v-toolbar-title class="black--text">
                Current Alarms
            </v-toolbar-title>
        </v-toolbar>
        <v-data-table
            :headers="headers"
            :items="selectedConds"
        >
             <template slot="items" slot-scope="props">
                <td>{{ props.item.alert }}</td>
                <td>
                    <v-checkbox
                        color="primary"
                        v-model="props.item.value"
                        class="mt-3"
                    ></v-checkbox>
                </td>
                <td>
                    <v-btn icon
                        @click="removePath(props.item)"
                    >
                        <v-icon color="error">
                            fas fa-trash
                        </v-icon>
                    </v-btn>

                    <v-btn icon
                    >
                        <v-icon color="primary">
                            fas fa-plus-circle
                        </v-icon>
                    </v-btn>

                      <v-btn icon
                        @click="removePath(props.item)"
                    >
                        <v-icon color="secondary">
                            fas fa-edit
                        </v-icon>
                    </v-btn>
                </td>
            </template>
        </v-data-table>
    </v-card>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex';

export default {
  name: 'AlarmsTable',
  props: {
    sensorNo: String,
  },
  data: () => ({
    headers: [
      { text: 'Alert', align: 'left', value: 'alert' },
      { text: 'Active?', value: 'active', sortable: false },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    addNew: false,
    conditionInput: '',
    boundaryInput: '',
    editCondition: false,
    conditionEdit: '',
    boundaryEdit: '',
    deleteAlert: false,

    livestockTypes: [
      'pig',
      'cow',
      'sheep',
    ],

    mobileTypes: [
      'quad',
      'tractor',
    ],

  }),
  computed: {
    ...mapGetters({
      paddocks: 'GET_PADDOCK_NAMES',
    }),

    ...mapState({
      neverAsk: 'neverAskDeletion',
      selected: 'selected',
      alarms: 'alarmConditions',
    }),

    selectedConds() {
      const condList = [];
      for (let i = 0; i < this.alarms.length; i += 1) {
        if (this.alarms[i].type === this.selected.type || this.alarms[i].type.includes(this.selected.type)) {
          condList.push(this.alarms[i]);
        }
      }
      return condList;
    },

  },
  methods: {
    ...mapMutations({
      never: 'CHANGE_DEL',
    }),

    addCondition() {
      this.conditions.push({
        text: this.conditionInput,
        value: false,
        priority: this.priorityInput,
        constraint: this.constraintInput,
        boundary: this.boundaryInput,
      });

      return this.addNew = false;
    },
    clearAll(conditions) {
      conditions.forEach((element) => {
        element.value = false;
      });
    },
    selectAll(conditions) {
      conditions.forEach((item) => {
        item.value = true;
      });
    },
    removeAlert(index) {
      this.$delete(this.conditions, index);
      this.deleteAlert = false;
    },

  },
};
</script>
