<script>
import { mapState, mapGetters } from 'vuex';
import AlarmsTable from './addons/AlarmsTable.vue';
import TrackMarker from '@/components/map/TrackMarker.vue';
import GmapCustomMarker from 'vue2-gmap-custom-marker';
import AssetMarker from '@/components/map/AssetMarker.vue';

export default {
  name: 'AssetConfig',
  computed: {
    ...mapState([
      'selected',
      'paddocks',
      'user'
    ]),
    ...mapGetters({
      padNames: 'GET_PADDOCK_NAMES',
    }),
  },
  data() {
    return {

      livestock: [
        'sheep',
        'cow',
        'pig',
      ],

      paddockList: [],

      fromPicker: null,
      edit: true,
      toPicker: null,
      select: 'always',
      addEmail: false,
      addPhone: false,
      currentPhone: '0452648681',
    };
  },
  components: {
    AlarmsTable,
    TrackMarker,
    AssetMarker,
    'gmap-custom-marker': GmapCustomMarker,
  },
  methods: {
       displayDrawTools() {
      const vm = this;
      const drawingManager = new google.maps.drawing.DrawingManager({
        drawingMode: google.maps.drawing.OverlayType.MARKER,
        drawingControl: true,
        drawingControlOptions: {
          position: google.maps.ControlPosition.TOP_CENTER,
          drawingModes: ['polygon', 'marker'],
        },
        markerOptions: { icon: 'https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png' },
      });
      drawingManager.setMap(this.$refs.map.$mapObject);
      google.maps.event.addListener(this.$refs.map, 'click', (event) => {
        const boundaries = event.overlay.getPaths();
        if (vm.newPaddock !== '') {
          vm.editPath({ name: vm.newPaddock, gps: boundaries.j[0].j });
          vm.newPaddock = '';
        } else {
          alert('New paddocks must have names.');
          event.overlay.setMap(null);
        }
      });
    },
  },
  mounted() {
      this.$gmapApiPromiseLazy().then(() => {
      const bounds = new google.maps.LatLngBounds();
      this.displayDrawTools();
    });
  }
};
</script>
